html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

html,
body {
  height: 100%;
}

body {
  min-width: 320px;
  overflow-x: hidden;
}

a {
  display: inline-block;
  text-decoration: none;
  color: inherit;
}

a, button {
  outline-color: transparent;
}

ul,
li {
  margin: 0;
  padding: 0;
}

body {
  font-family: 'NotoSans';
  font-size: 19px;
  line-height: 24px;
}

