/* header */
    .header{
    	display: flex;
        position: relative;
        width: 100%;
        height: 1134px;
        color: #fff;
        background-image: url('../img/header/slide1.jpg');
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center;
        
        &::before{
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: calc(100% - -42px);
            background: rgba(147,126,108,.6);
            box-shadow: inset 0 4px 250px #937e6c, inset 0 4px 250px #937e6c;
            z-index: 0;
        }

        &::after{
            position: absolute;
            width: 100%;
            height: 390px;
            content: '';
            bottom: -42px;
            left: 0;
            background: url("data:image/svg+xml;charset=UTF-8,%3csvg xmlns='http://www.w3.org/2000/svg' width='1600' height='795'%3e%3cpath d='M800 177.8c-163.9 145.1-331.8 184.7-474.1 169-8.9-1-19.3-2.4-19.3-2.4C244.4 336 198.2 329.7 152 308c-52.9-24.8-88.2-61.4-120.9-95.3-13.3-13.8-23.8-26-31.1-34.9V795h1600V177.8c-27.4-34.5-81.4-92.8-168-133.8-18.2-8.6-110.6-50.9-235.6-44C1061.1 7.5 977.8 20.3 800 177.8z' fill='%23FBFAF8'/%3e%3c/svg%3e");
            background-position-x: center;
            z-index: 0;
        }
    }
    .header__top{
    	max-width: 1600px; 
    	width: 100%;
    	height: 120px;
    	display: flex;
        justify-content: space-between;
        align-items: center;
        margin-left: auto;
        margin-right: auto;
        z-index: 1;
        
        &-contacts{
        	color: #fff;
        	a{
        		display: flex;
    	        align-items: center;
    	        margin-bottom: 3px;
        	}
        	span{
        		color: #fff;
        		font-size: 11px;
        		display: flex;
    	        align-items: center;
           	    justify-content: flex-end;
        	}
        }
        &-logo{
            img{
                width: 200px;
                height: 44px;
            }
            
        }
        &-menu{
        	color: #FFF;
        	display: flex;
            align-items: center;
        }
    }

    /* icons */
    .phoneIcon{
    	width: 30px;
    	height: 30px;
    }
    .socialIcon{
    	width: 17px;
    	height: 16px;
    }
    .whatsApp{
    	margin-left: 5px;
    	margin-right: 10px;
    }
    .menuIcon{
    	width: 29px;
    	height: 19px;
    	margin-left: 7px;
    	cursor: pointer;
    }
    .services__img{
        height: 65px;
        position: absolute;
        max-width: 100%;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        margin: auto;
    }
    /* icons-end */
    
    .header__text{
        width: 50%;
        display: flex;
        align-items: center;
        margin-top: 102px;
        position: absolute;
        left: 0;
        right: 0;
        margin-left: auto;
        margin-right: auto;
        
        &-title{
            font-family: 'MontserratAlternates-ExtraBold';
            font-size: 35px;
            max-width: 654px;
            line-height: 32px;
            position: relative;
            padding-top: 10px;
            
            &::after{
                content: '';
                position: absolute;
                top: 5px;
                right: 0;
                width: 3px;
                height: 89px;
                background-image: url('../img/svg/title-separator.svg');
                background-repeat: no-repeat;
            }
        }
        &-desc{
            padding-left: 32px;
            font-size: 20px;
            text-shadow: 0 4px 10px rgba(255,255,255,.25);
        }
    }
    .services-title{
        position: absolute;
        bottom: -130px;
        max-width: 864px;
        font-weight: 700;
        line-height: normal;
        font-size: 25px;
    }

    .services-wrap{
        max-width: 1030px;
        width: 100%;
        height: 700px;
        border: 1px solid red;
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
        margin: 0 auto;
    }
    .services{
        max-width: 340px;
        width: 100%;
        height: 100%;
        border: 1px solid green;
        position: relative;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
    }
    .services__item{
        width: 136px;
        height: 136px;
        background-image: url('../img/svg/circle.svg');
        position: relative;
        top: -20px;
        
        &-item1{
           left: 120px; 
        }
        &-item2{
           left: 50px; 
        }
        &-item3{
           left: 50px; 
        }
        &-item4{
           left: 120px; 
        }

    }
    .services__desc{
        max-width: 152px;
        width: 100%;
        font-size: 16px;
        line-height: 20px;
        z-index: 1;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(35%, -50%);
        display: block;
        span{
            background-color: #FFF;
            color: #000;
            padding: 0px 5px 4px;
        }
    }

    .feedback {
        position: absolute;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        top: 0px;
        left: 355px;
        width: 605px;
        height: 605px;
        border-radius: 50%;
        background-image: linear-gradient(146.57deg,rgba(255,255,255,.28) 14.73%,rgba(255,255,255,.81) 65.62%);
    }
    .feedback-wrap {
        position: relative;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        align-items: center;
        width: 510px;
        height: 510px;
        border-radius: 50%;
        border: 20px solid rgba(243,238,233,.83);
        background-color: #fff;
        z-index: 2;
    }

    .feedback__icon{
        width: 55px;
        height: 55px;
        background-image: url('../img/svg/feedback_icon.svg');
    }
    .feedback__title{
        width: 328px;
        font-family: 'MontserratAlternates-SemiBold';
        line-height: normal;
        font-size: 21px;
        text-align: center;
        color: #322823;
    }
    .feedback__form {
        top: -10px;
        display: flex;
        flex-direction: column;
        align-items: center;
        min-height: 200px;
        justify-content: space-around;
    }
    .feedback__input {
        position: relative;
        border: none;
        font-size: 19px;
        
        &::after{
            content: '';
            position: absolute;
            bottom: 2px;
            left: 0;
            width: 343px;
            height: 3px;
            background-color: rgba(50,40,35,.7);
            border-radius: 50px;
        }
    }
    .feedback__input-input {
        position: relative;
        width: 343px;
        height: 39px;
        border: none;
        background-image: url('../img/svg/feedback_input_bg.svg');
        background-repeat: no-repeat;
        background-position: 100%, 50%;
        
        &:focus{
            outline-color: transparent;
        }
    }
    .btn {
        padding: 14px 46px;
        background: linear-gradient(90deg,#f48922 0,#ffb015 100%);
        border: none;
        border-radius: 50px;
        font-size: 19px;
        color: #fff;
        box-shadow: 0 8px 35px rgba(254,171,23,.7);
        cursor: pointer;
        transition: all .9s;
        
        &:hover{
            background: linear-gradient(90deg,#f17c0c 0,#fba600 100%),50%;
        }
    }
    
    .checkbox {
        position: relative;
        display: flex;
        align-items: center;
    }

    .checkbox input[type=checkbox] {
        visibility: hidden;
    }
    .checkbox__label {
        position: absolute;
        top: 0;
        left: 0;
        min-height: 20px;
        min-width: 20px;
        width: 21px;
        height: 21px;
        background-color: transparent;
        border: 1px solid #322823;
        border-radius: 50%;
        cursor: pointer;
        
        &::after{
            position: absolute;
            top: 6px;
            left: 4px;
            width: 10px;
            height: 4px;
            border: 2px solid #322823;
            border-top: none;
            border-right: none;
            content: "";
            opacity: 1;
            transform: rotate(-45deg);
        }
    }
    .checkbox__label:after {
    position: absolute;
    top: 6px;
    left: 4px;
    width: 10px;
    height: 4px;
    border: 2px solid #322823;
    border-top: none;
    border-right: none;
    content: "";
    opacity: 0;
    -webkit-transform: rotate(-45deg);
    transform: rotate(-45deg);
}
    .checkbox__desc_round-feedback {
        padding-left: 14px;
        width: 190px;
        height: 48px;
        font-size: 13px;
        margin-top: -5px;
    }
    .checkbox__descr {
        padding-left: 10px;
        font-weight: 200;
        line-height: 16px;
        font-size: 13px;
        color: #322823;
        
        span{
            cursor: pointer;
        }
    }
/* header-end */
